  name: 'SearchHistory',
  props: {
    searchHistories: {
      type: Array,
      required: true
    }
  },
  components: {},
  data  () {
    // 
    return {
      isEdit: false
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
