<!--  -->
<template>
<div class=''>
    <van-list
    v-model="loading"
    :finished="finished"
    finished-text=""
    @load="onLoad"
    >
        <van-cell v-for="(item,index) in list" :key="index"
        :title="item.title"
        ></van-cell>
    </van-list>
</div>
</template>

<script>
import { getResult } from '@/api/search'
// jsjsjson
// import  from '';

export default {
// import
  name: 'SearchResult',
  props: {
    searchText: {
      type: String,
      required: true
    }
  },
  components: {},
  data  () {
    // 
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  // 
  methods: {
    async onLoad () {
      // 1.
      const res = await getResult({
        page: this.page,
        per_page: 10,
        q: this.searchText
      })
      // 2.
      this.list.push(...res.data.data.results)
      this.page++
      // 3.
      this.loading = false
      // 4.
      if (res.data.data.results.length === 0) {
        this.finished = true
      }
    }
  },
  //  - this
  created  () {

  },
  //  - DOM
  mounted  () {

  },
  beforeCreate  () {}, //  - 
  beforeMount  () {}, //  - 
  beforeUpdate  () {}, //  - 
  updated  () {}, //  - 
  beforeDestroy  () {}, //  - 
  destroyed  () {}, //  - 
  activated  () {} // keep-alive
}
</script>
<style lang='less' scoped>
